<template>
  <div class="container">
    <el-header class="page-header">
      我的粉丝
    </el-header>
    <ul v-if="followers.length">
      <li v-for="followee in followers" > <router-link :to="{ name: 'User', params: { id: followee.id } }">{{ followee.get('username') }}</router-link></li>
    </ul>
    <span v-else>你没有粉丝</span>
  </div>
</template>

<script>
  import { mapState } from 'vuex'
  export default {
    name: "myFollower",
    data(){
      return {
        followers: []
      }
    },
    computed: mapState(['user']),
    created(){
      this.getFollower()
    },
    methods: {
      getFollower(){
        const q = this.user.followerQuery();
        q.include('follower');
        q.find().then((followers)=>{
          this.followers = followers;
          this.$Progress.finish();
        }).catch(err=>console.log(err))
      }
    }
  }
</script>

